.app{
    height: 100vh;
    display: flex;
    background-color: rgb(5, 11, 31);
    color:white
}
.main{
    width: 75%;
    background: linear-gradient(to bottom,rgba(0,0,0,0),#020230),
     url("./assets/back.jpg") center;
     flex-direction: column;

}
.top{
    height: 50%;
    position: relative;
}
.timer{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 5px solid white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    font-weight: 700;
    position: absolute;
    left: 80px;
    bottom: 10px;
}
.bottom{
    height: 50%;
}
.trivia{
    height:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.question{
    width: 80%;
    background:linear-gradient(#100241,black);
    border: 2px solid wheat;
    text-align: center;
    padding: 20px;
    border-radius: 10px;
    font-size: 22px;
}
.answers{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

}
.answer{
    width: 40%;
    padding:10px ;
    text-align: center;
    background: linear-gradient(#0e0112,#1d5d83c9);
    border: 1px solid whitesmoke;
    border-radius: 15px;
    font-size: 20px;
    font-weight: 300;
    cursor: pointer;
    margin: 0 10px 20px 10px;

}

.pyramid{
    width: 25%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.moneyList{
    padding:10vh;
    list-style: none;
    height: 100%;

    
}
.answer:hover,.answer.active{
    background: mediumblue;
}
.answer.correct{
    animation:correct 3s ease forwards
}
@keyframes correct{
    0%,22%,42%{
        background: mediumslateblue;
    
    }
    20%,40%,60%{
        background: linear-gradient(#0e0124,#22074d);
    }
    62%,100%{
        background: green;
    }
}

.answer.wrong{
    animation:wrong 3s ease forwards
}
@keyframes wrong{
    0%,22%,42%{
        background: mediumslateblue;
    
    }
    20%,40%,60%{
        background: linear-gradient(#0e0124,#22074d);
    }
    62%,100%{
        background: crimson;
    }
}

.moneyListItem{
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 5px;
    margin: auto;

}
.moneyListItem.active{
    background-color: chocolate;

}

.moneyListItemNumber{
    font-size: 18px;
    font-weight: 100;
    width: 80%;

}
.moneyListItemAmount{
    font-size: 20px;
    font-weight: 300;
    width: fit-content;
}

.endText{
    position: relative;
    top: 50%;
    bottom: 0;
    left: 25%;
    right: 0;
    margin: auto;
}

.start{
    width: 250px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
}
.startInput{
    width: 100%;
    height: 33px;
    border: none;
    border-radius: 6px;
    text-align: center;
    font-size: 18px;
}
.startInput:focus{
    outline: none;
}
.startButton{
    width: 100%;
    height: 30px;
    border: none;
    font-size: 18px;
    font-weight: 400;

}